<template>
  <section>
    <!-- <main> -->
      <h1 class="headline text-center">Praktikataotlus</h1>

      <div class="internship-form">
        <form @submit.prevent>
          <div id="student-form">
            <div>
              <h3>Praktikandi andmed:</h3>
              <div class="form-item">
                  <label>√ïpilase nimi</label>
                  <input v-model="nimi" type="text" />
              </div>
              <div class="form-item">
                  <label>Eriala/Valdkond</label>
                  <input v-model="eriala" type="text" />
              </div>
              <div class="form-item">
                  <label>Praktikaperiood</label>
                  <input v-model="praktikaperiood" type="text" />
              </div>
              <div class="form-item">
                  <label>Praktika maht astronoomilistes tundides EKAP-tes</label>
                  <input v-model="praktika_maht" type="text" />
              </div>
              <div class="form-item">
                  <label>Ettev√µtte esindaja e-post:</label>
                  <input v-model="ettevote_email" type="email" />
              </div>
            </div>

            <div class="tasks">
              <h3>√úlesanded:</h3>
              <div class="form-item" v-for="(task, index) in tasks" v-bind:key="index">
                <input type="text" :ref="'task'" v-model="task.title"/>

                <span class="control" v-if="index + 1 === tasks.length" @click="addTask"><img src="../assets/plus.svg"></span>
                <span class="control" v-show="index > 0" @click="removeTask(index)"><img src="../assets/minus.svg"></span>
                <!-- <div>{{task}}</div> -->
              </div>
            </div>
          </div>
          <div class="gdpr">
            <!-- <label>
              <input type="checkbox" name="gdpr" id="gdpr" v-model="gdpr">
              Olen tutvunud ja n√µustun <a href="#">privaatsustingimustega</a>.
            </label> -->
            <input type="checkbox" name="gdpr" id="gdpr" v-model="gdpr" required>
            <label for="gdpr">Olen tutvunud ja n√µustun <a href="/privaatsustingimused" target="_blank">privaatsustingimustega</a>.</label>
          </div>
          <!-- <button class="send_button" type="submit">Saada</button> -->
          <input class="send_button" type="submit" value="Saada" @click="SendApplication">
        </form>
      </div>
    <!-- </main> -->
  </section>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'

export default {
  name: "studentApplication",
  data() {
    return {
      nimi: '',
      eriala: '',
      praktikaperiood: '',
      praktika_maht: '',
      ettevote_email: '',
      tasks: [],
      blockRemoval: true,
      gdpr: false,
    }
  },
  watch: {
    tasks () {
      this.blockRemoval = this.tasks.length <= 1
    }
  },
  methods: {
    addTask () {
      let checkEmptyTasks = this.tasks.filter(task => task.title === null)
      if (checkEmptyTasks.length >= 1 && this.tasks.length > 0) return
      
      this.tasks.push({title: null})
      
      if (this.tasks.length > 1) {
        this.$nextTick(() => {
          let index = this.tasks.length - 1;
          let input = this.$refs.task[index];
          input.focus()
        })
      }

    },
    removeTask (taskId) {
      if (!this.blockRemoval) this.tasks.splice(taskId, 1)
    },
    SendApplication() {
      // const token = localStorage.getItem('token');
      const options = { 
        headers: {
        'Content-Type': 'application/json',
        // 'x-auth-token': `${token}`
        }, withCredentials: true}

      axios.post('http://localhost:5000/api/taotlus', {
        opilase_nimi: this.nimi,
        eriala: this.eriala,
        periood: this.praktikaperiood,
        maht: this.praktika_maht,
        ettevote_email: this.ettevote_email,
        ulesanded: this.tasks.map(t => t.title)
      }, options)
      .then((res) => this.message = res.msg)
      .catch((error) => {
        this.message = error.error;
      })

      // location.reload(true)

      this.nimi = '';
      this.eriala = '';
      this.praktikaperiood = '';
      this.praktika_maht = '';
      this.ettevote_email = '';
      this.tasks  = []
      this.addTask()
      this.gdpr = false;
    },
    userInfo() {
      axios.get('http://localhost:5000/api/users/me', {withCredentials: true})
      .then((res) => {
        console.log(`Student Logged in:`, res)
        this.nimi = res.data.name;

      if (res.data.group === 'vs') {
        this.eriala = 'Noorem tarkvaraarendaja'
        } else if (res.data.group === 'kd') {
          this.eriala = 'Kasutajakogemuse disaini nooremspetsialist'
        } else if (res.data.group === 'is') {
          this.eriala = 'IT-s√ºsteemide spetsialist'
        } else if (res.data.group === 'isp') {
          this.eriala = 'IT-s√ºsteemide spetsialist'
        } else if (res.data.group === 'syst') {
          this.eriala = 'S√ºsteemiadministraator'
        } else if (res.data.group === 'ita') {
          this.eriala = 'Tarkvaraarendaja'
        }
      }).catch((err) => {
        throw err
      })
    }
  },
  created() {
    this.userInfo();
  },
  mounted() {
    this.addTask()
  }
};
</script>



