<template>
    <div class="pdf-mask" v-if="showPDF">
        <div class="header">
            <span @click="showPDF = false">X</span>
        </div>
        <PDFItem />
    </div>
</template>

<script>
import PDFItem from './PDFItem'
export default {
    components: {
        PDFItem
    },
    data() {
        return {
            showPDF: true
            // pdfUrl: 'https://www.cl.cam.ac.uk/teaching/Lectures/funprog-jrh-1996/all.pdf'
        }
    }
}
</script>

<style lang="scss" scoped>

    .pdf-mask {
        position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .75);
        display: block;
        transition: opacity .3s ease;

        .header {
            padding: 1rem 0;
            color: #ffffff;
            // background: rgb(82, 86, 89);
            background: rgb(50, 54, 57);
            span {
                cursor: pointer;
                display: inline-block;
                position: absolute;
                right: 1rem;
                top: .5rem;
            }
        }
    }
</style>