import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router'
import axios from 'axios'
// import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex)

export default new Vuex.Store({
  // plugins: [createPersistedState()],
  namespaced: true,
  state: {
    token: '',
    user: null,
    documents: [],
    upload: {
      error: null,
      message: null,
      file: null,
      filename: 'Lisa uus dokument'
    }
  },
  getters: {
    authenticated (state) {
      return state.token && state.user
    },
    user(state) {
      return state.user
    },
    documents (state) {
      return state.documents
    },
    // upload(state) {
    //   return state.upload
    // },
    filename(state) {
      return state.upload.filename
    },
    file(state) {
      return state.upload.file
    },
    upload_message(state) {
      return state.upload.message
    },
    upload_error(state) {
      return state.upload.error
    }
  },
  mutations: {
    SET_TOKEN(state, token) {
      state.token = token;
    },
    SET_USER(state, data) {
      state.user = data;
    },
    LOGOUT(state) {
      state.token = null;
      state.user = null;
    },
    SELECT_DOCUMENT(state, payload) {
      state.upload.file = payload.file;
      state.upload.filename = payload.filename;
    },
    SET_DOCUMENT(state, documents) {
      state.documents = documents;
      // commit('SET_UPLOAD_STATUS', false, '')
      // commit('SET_UPLOAD_STATUS', false, '', 'fail')
    },
    SET_UPLOAD_STATUS(state, payload) {
      state.upload.error = payload.error;
      state.upload.message = payload.message;
      // state.upload.file = name
    }


    // SET_UPLOAD_STATUS(state, error, message, name) {
    //   state.upload.error = error;
    //   state.upload.message = message;
    //   state.upload.file = name
    // }

  },
  actions: {


    async getDocuments({commit}) {
       let response = await axios.get('/api/documents', {withCredentials: true});
       commit('SET_DOCUMENT', response.data)
    },

    async selectDocument({commit}, payload) { 
      if (!payload.file) {
        commit('SET_UPLOAD_STATUS', true, 'Fail puudub')
      } else {
        commit('SELECT_DOCUMENT', payload)
        commit('SET_UPLOAD_STATUS', false, null)
      }
    },

    async uploadDocument({commit, dispatch}, formData) {
      try {
        await axios.post('/api/documents/', formData, {withCredentials: true})
        commit('SET_UPLOAD_STATUS', {error: false, message:'Fail √ºles laetud'})
        await dispatch('getDocuments')

        setTimeout(() => {
          commit('SET_UPLOAD_STATUS', {error: false, message: null})
        }, 5000)

      }catch(err) {
        commit('SET_UPLOAD_STATUS', {error: true, message:'Midagi l√§ks valesti. Proovi uuesti.'})

        // setTimeout(() => {
        //   commit('SET_UPLOAD_STATUS', {error: true, message:'Midagi l√§ks valesti. Proovi uuesti'})
        // }, 5000)
      } finally {
        commit('SELECT_DOCUMENT', {file: null, filename: 'Lisa uus dokument'})
      }
    },

    async removeDocument({dispatch}, document) {
      await axios.delete(`/api/documents/${document._id}`, {withCredentials: true});
      dispatch('getDocuments')
    },

    async signIn ({ dispatch }, credentials) {
      const options = { headers: {'Content-Type': 'application/json'}, withCredentials: true}
      await axios.post('/api/auth/login', credentials, options)

      dispatch('attempt')
    },

    async attempt({ commit }) {

      // commit('SET_TOKEN', token)

      try {
        let {data} = await axios.get('/api/users/me', {withCredentials: true})
          const role = data.role;
          
          commit('SET_USER', data)
          if (role === 'admin') {
            router.push('/teacher')
          } else {
            router.push('/student')
          }

        // let response = await axios.get('http://localhost:5000/api/users/me', {
        //   headers: {
        //     'x-auth-token': token
        //   }
        // }).then((res) => {
        //       const role = res.data.role;
    
        //       if (role == 'admin') {
        //           router.push('/teacher')
        //       } else {
        //           router.push('/student')
        //       }
        //   })

      }catch(error) {
        commit('SET_TOKEN', null)
        commit('SET_USER', null)
      }
      // axios.get('http://localhost:5000/api/users/me',
      // {
      //     headers: {'x-auth-token': `${token}`}
      // }).then((res) => {
      //     const role = res.data.role;

      //     if (role == 'admin') {
      //         this.$router.push('/teacher')
      //     } else {
      //         this.$router.push('/student')
      //     }
      // })
    }
  },
  modules: {
  }
})
