<template>
  <div class="flex-column">
    <!-- <div> -->
      <BaseAccordian>
        <template v-slot:title>{{ filterTitles }}</template>

        <template v-slot:content>

          <p v-if="filteredApplications.length < 1">Kahjuks ei ole mingit infot veel kuvada.</p>

          <div class="item" v-for="application in filteredApplications" :key="application._id">
            <p>Nimi: {{application.opilase_nimi}}</p>             
            <a :href="URL(application)" target="_blank">Praktikataotlus</a> 
            <p>Kuup√§ev: {{localDateFormat(application.created)}}</p>
            <!-- <p>Kuup√§ev: {{application.created}}</p> -->
          </div>
        </template>
      </BaseAccordian>
    <!-- </div> -->
  </div>
</template>
<script>
import BaseAccordian from "./BaseAccordion";
import moment from 'moment'

export default {
  props: {
    taotlused: {
      type: Array,
      required: true
    },
    title: {
      type: String,
      required: true
    }
  },
  components: {
    BaseAccordian
  },
  data() {
    return {
    };
  },
  methods: {
    URL(i) {
      return `http://localhost:5000/taotlused/${i._id}/${i.opilase_nimi} ${moment(i.created).format('DD.MM.YYYY')}.pdf`
    },
    localDateFormat(date) {
      return moment.utc(date).format('DD.MM.YYYY');
    },
    yearDiff(date) {
      // return moment().diff(moment(`${date.split('.')[1]}.${date.split('.')[0]}.${date.split('.')[2]}`), 'years')
      // return moment.utc().diff(moment.utc().add(1, 'years'), 'years')
      return moment.utc().diff(moment.utc(date), 'years')
    }
  },
  computed: {
    filteredApplications() {      
      return this.taotlused.filter(i => i.user.group === this.title.toLowerCase() && this.yearDiff(i.created) < 1)
      // return this.taotlused.filter(i => i.user.group === this.title.toLowerCase());
    },
    filterTitles() {
      let title = '';
      if (this.title == 'VS') {
        title = 'Noorem tarkvaraarendaja (VS)'
      } else if (this.title == 'KD') {
        title = 'Kasutajakogemuse disaini nooremspetsialist (KD)'
      } else if (this.title == 'IS') {
        title = 'IT-s√ºsteemide spetsialist (IS)'
      } else if (this.title == 'ISP') {
        title = 'IT-s√ºsteemide spetsialist (ISP)'
      } else if (this.title == 'SYSt') {
        title = 'S√ºsteemiadministraator (SYSt)'
      } else if (this.title == 'ITA') {
        title = 'Tarkvaraarendaja (ITA)'
      }

      return title;
    }
  },
  created() {
    // console.log(this.filteredApplications)
    // console.log(this.URL)
  }
};
</script>

