<template>
    <div class="flex-column">
        <!-- {{filteredDocuments}} -->
        <div v-for="document in filteredDocuments" :key="document._id">
            <!-- {{data}} -->
            <!-- {{document}} -->
            <div class="item">
                <!-- <p>{{document.doc}}</p> -->
                <p>{{documentName(document)}}</p>
                <div class="buttons">
                <!-- <a href="http://localhost:3344/static/" + i.filename>Lae alla</a> -->
                    <!-- <a :href="URL(document)" target="_blank">Lae alla</a> -->
                    <a :href="URL(document)" target="_blank">Vaata</a>
                    <button class="delete-button" v-if="isAdmin()" :data-id="document._id" @click="removeDocument(document)">Kustuta</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
    name: 'documents',
    props: {
        data: Array,
        group: {
            type: String,
            default: 'all'
        },
        docType: String,
    },
    computed: {
        filteredDocuments() {
         return this.data.filter((document) => document.group === this.group.toLowerCase() && document.docType === this.docType);
        //  return this.data.filter((i) => i.group === this.group.toLowerCase());
        },
    },
    methods: {
        ...mapActions({
            removeDocument: 'removeDocument'
        }),
        isAdmin() {
            return this.$store.state.user.role === 'admin' ? true : false
        },
        URL(i) {
            return `http://localhost:5000/${i.path}`
        },
        documentName(document) {
            if (!document.docName) {
                return document.doc;
            } else {
                return document.docName
            }
            // return !document.docName ? document.docName : document.doc
        },
    },
}
</script>

<style lang="scss" scoped>

.flex-column > div {
    width: 100%;
    // padding: 0 20px 20px 20px;
    padding: 0 0 30px 0;

    &:last-of-type {
        padding-bottom: 0;
    }

    .item {

        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        p {
            font-size: 0.925rem;
            line-height: 1.5rem;
            word-break: break-word;
            margin-right: 10px;
        }

        .buttons {
            display: flex;
            flex-direction: column;

            a {
                margin-bottom: 10px;
            }

            // a + a, a + button {
            //     // margin-left: 10px;
            // }

            a {
                color: #ffe600;
                font-weight: 900;
                border-radius: 50px;
                border: 1px solid #ffe600;
                padding: .5rem 1.5rem;
                padding: .5rem 1rem;
                font-size: 0.875rem;
                min-width: 110px;
                text-align: center;
            }

            button {
                    color: #ffe600;
                    background: transparent;
                    outline: none;
                    cursor: pointer;
                    font-weight: 900;
                    border-radius: 50px;
                    border: 1px solid #ffe600;
                    padding: .5rem 1.5rem;
                    padding: .5rem 1rem;
                    font-size: 0.875rem;
                    min-width: 110px;
                    text-align: center;
            }
        }
    }
}

@media (min-width: 425px) {

    .flex-column > div {
        .item {
            p {
                font-size: 1rem;
            }
        }
    }
}

@media (min-width: 992px) {
    .flex-column > div {
        .item {
            .buttons {
                flex-direction: row;

                a {
                    margin-bottom: 0;
                }

                a + a, a + button {
                    margin-left: 10px;
                }
            }
        }
    }
}

