import Vue from 'vue'
import VueRouter from 'vue-router'
// import * as store from '../store'
import Login from '../views/Login.vue'
import Student from '../views/Student.vue'
import Teacher from '../views/Teacher.vue'
import InternshipApplication from '../views/InternshipApplication.vue'
import CompanyApplication from '../views/CompanyApplication.vue'
import PrivacyPolicy from '../views/PrivacyPolicy.vue'
import NotFound from '../views/404.vue'
import test from '../views/test.vue'
import PDF from '../views/Pdf.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/student',
    name: 'student',
    component: Student,
    // beforeEnter(to, from, next) {
    //   // if (localStorage.getItem('token')) {
    //   if (store.getters.authenticated) {
    //     next();
    //   } else {
    //     next({name: 'login'});
    //   }
    // }
  },
  {
    path: '/teacher',
    name: 'teacher',
    component: Teacher,
    // beforeEnter(to, from, next) {
    // //   if (localStorage.getItem('token')) {
    //   if (store.getters.authenticated) {
    //     next();
    //   } else {
    //     next({name: 'login'});
    //   }
    // }
  },
  {
    path: '/praktikataotlus',
    name: 'praktikataotlus',
    component: InternshipApplication
  },
  {
    path: '/taotlus/:id',
    name: 'taotlus',
    component: CompanyApplication
  },
  {
    path: '/test',
    name: 'test',
    component: test
  },
  {
    path: '/pdf/:id',
    name: 'pdf',
    component: PDF,
    meta: { hideHeader: true, hideFooter: true}
  },
  {
    path: '/privaatsustingimused',
    name: 'privaatsustingimused',
    component: PrivacyPolicy
  },
  {
    path: '/*',
    name: '404',
    component: NotFound
  }
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
